.POPOSSpace {
    position: relative;
    /* width: 100%;
    text-align: center;
    background-color: #bce1eb;
    padding: 10px;
    margin-bottom: 1rem; */

  }
  
  .POPOSSpace .POPOSSpace-title {
    text-decoration: none;
    color: #000;
  }

  .POPOSSpace h1 {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    font-weight: lighter;
    background-color: rgba(255, 255, 255, 0.75);
    padding: 1rem;
    text-align: left;
  }
  
  .POPOSSpace img {
    width: 100%;
    height: auto;
    padding-top: 10px;
  }

  .POPOSSpace .POPOSSpace-info {
    position: absolute;
    right: 0;
    bottom: 0;
    text-align: right;
  }
  
  .POPOSSpace .POPOSSpace-info  {
    display: inline-block;
    padding: 0.25em 0.5em;
    background-color: rgba(255, 255, 255, 0.9);
  }
  

  
.POPOSList {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2em;
	max-width: 1200px;
    margin: auto;
    padding: 2em;
}

.container {
    background-color:  #e2e2c8;
    

}

.POPOSList form {
	display: flex;
	align-items: flex-start;
}

.POPOSList form input, .POPOSList form button {
	padding: 1em;
	margin: 0;
	border: 0;
	font-size: 1rem;
}

.POPOSList form input {
	flex: 1 1;
	border-top-left-radius: 0.5em;
	border-bottom-left-radius: 0.5em;
}

.POPOSList form button {
	border-top-right-radius: 0.5em;
	border-bottom-right-radius: 0.5em;
}

/* Smaller screens and tablets */

@media only screen and (max-width: 960px) {
	.POPOSList {
		grid-template-columns: 1fr 1fr;
	}
}

/* Small screens mobile */
@media only screen and (max-width: 700px) {
	.POPOSList {
		grid-template-columns: 1fr;
	}
}

.POPOSDetails {
	display: flex;
	flex-direction: row-reverse;
	width: 960px;
	margin: auto;
    margin-top: 2rem;
}

.POPOSDetails .POPOSDetails-info {
	flex: 1 1;
	text-align: left;
	padding: 0 1em;
}

.POPOSDetails .POPOSDetails-title {
	font-size: 2em;
	margin: 0;
}

.POPOSDetails-image {
	flex: 2 1;
}

.POPOSDetails .POPOSDetails-image img {
	width: 100%;
}

.POPOSDetails-hours {
	font-size: 1em;
	padding:1rem;
}

.POPOSFeature {
	font-size: 2em;
	padding: 0.25em;
	margin: 0.125em;
	border-radius: 0.25em;
	background-color: #eee;
}
.POPOSFeatureList {
	display: flex;
	margin-top: 1rem;
}
